<script>
  import Vue from 'vue'
  import { VueTypedJs } from 'vue-typed-js'
  import cover from '../components/cover.vue'
  import carousel from 'vue-owl-carousel'
  import { TweenMax , Power2, Circ, CSSPlugin, AttrPlugin } from 'gsap/all'
  import { EventBus } from '../event-bus'

  const plugins = [ CSSPlugin, AttrPlugin ]
  
  Vue.use(VueTypedJs)
  
  export default {
    metaInfo: {
      // 페이지 제목 설정
      title: 'Intro',
      // 제목 템플릿 설정: "Vue 앱의 검색엔진 최적화 | yamoo9.gitbook.io/vue-a11y-seo"
      titleTemplate: '%s < Mok, Sunung',
      // <html> 요소의 속성 설정
      htmlAttrs: {
        // 주 언어 명시
        lang: 'ko-KR',
        dir: 'ltr'
      }
    },
    components: {
      cover,
      VueTypedJs,
      carousel,
    },
    methods: {
      track () {
        this.$ga.page('/')
      },
      beforeChangeCarousel: function(){
        var target = document.querySelectorAll('.skil_desc_item');
        target.forEach(function(element) {
          element.classList.remove('active');
          var everybars = element.querySelectorAll('.skill_bar_fill_inner');
          everybars.forEach(function(bar){
            TweenMax.to(bar, 0.5, { width: 0, ease: Power2.easeInOut });
          })
        });
      },
      changeCarouselData: function(){
        var target = document.querySelectorAll('.skil_desc_item');
        var actived = document.querySelector('.owl-item.center');
        var index = parseInt(actived.querySelector('.skill_item').getAttribute('data-index'));
        target[index].classList.add('active');
        var bars = target[index].querySelectorAll('.skill_bar_item');
          bars.forEach(function(bar) {
            var percent = bar.getAttribute('data-skill-percent');
            var item = bar.querySelector('.skill_bar_fill_inner');
            TweenMax.to(item, 1, { width: percent + '%', ease: Power2.easeInOut });
          });
      },
      handleScroll: (event, el) => {
        let scroll = window.scrollY;
        let bottom = scroll + document.body.clientHeight;
        let anchor = bottom - 50;
        let offsetValue = window.pageYOffset + el.getBoundingClientRect().top;
        if (anchor >= offsetValue) {
          el.classList.add('on');
        } else {
          el.classList.remove('on');
        }
      },
      handleScrollForward: function(event, el) {
        let scroll = window.scrollY;
        let bottom = scroll + document.body.clientHeight;
        let anchor = bottom - 50;
        let offsetValue = window.pageYOffset + el.getBoundingClientRect().top;
        if (anchor >= offsetValue) {
          el.classList.add('on');
        }
      },
      scrollMoveTo: function (to, duration) {
        TweenMax.to(window, duration/1000, { scrollTo:{y:to}, ease: Circ.easeInOut });
      },
      scrollMoveContent: function(duration){
        let eleOffsetTop = this.$refs.pageTop.clientHeight;
        eleOffsetTop = window.innerWidth < 992 ? eleOffsetTop : eleOffsetTop - 80;
        this.scrollMoveTo(eleOffsetTop, duration)
      },
    },
    beforeCreate() {
      EventBus.$emit('pageName', 'intro')
    },
    mounted() {
      this.changeCarouselData();
    }
  }
</script>

