<template>
  <div id="app" :data-page-path="this.pageName2" ref="wrapper">

    <div class="backside_navigation">
      <div class="inner">
        <div class="mobile_button" @click="toggleMenu('toggle')">
          <button class="gnb_switch">
            <span>메뉴</span>
          </button>
        </div>
      </div>
      <div class="wrap_mui">
        <div class="inner navigation_area">
          <pageHeader :pageTitle="this.pageName2"></pageHeader>
        </div>
        <div class="btn_credit">
          <router-link to="/credit">
            CREDIT<i class="fa fa-long-arrow-right" aria-hidden="true"></i>
          </router-link>
        </div>
      </div>
      <div class="backside_closer" @click="toggleMenu('close')"></div>
    </div>

    <div class="wrapper">
      <header :class="'header' + ' ' + this.pageName2">
        <div class="inner">
          <div class="header_original">
            <pageHeader :pageTitle="this.pageName2"></pageHeader>
          </div>
        </div>
        <div class="sticky_header" v-scroll="handleScroll">
          <pageHeader position="header_contents" :pageTitle="this.pageName2"></pageHeader>
        </div>
      </header>


      <transition name="viewer" mode="out-in">
        <router-view :key="'main-router'"></router-view>
      </transition>
       <!-- :key="$route.fullPath" -->

      <pageFooter :pageTitle="this.pageName2"></pageFooter>
    </div>

  </div>
</template>

<script>
  import pageHeader from './components/header.vue';
  import pageFooter from './components/footer.vue';
  import { EventBus } from './event-bus'
  // import { TweenMax } from 'gsap/all'

  export default {
    components: {
      pageHeader,
      pageFooter
    },
    props: {
      pageName: String
    },
    data() {
      return {
        pageName2: null,
        
      }
    },
    methods: {
      handleScroll: function(evt, el) {
        let scroll = window.scrollY;
        let coverMoveAmount = document.querySelector('section.intro').clientHeight - el.clientHeight;

        if (scroll >= coverMoveAmount) {
          el.classList.add('on');
        } else {
          el.classList.remove('on');
        }
      },
      toggleMenu(order) {
        let wrapper = this.$refs.wrapper;
        if (order === 'open') {
          wrapper.classList.add('menu-on');
        } else if (order === 'close') {
          wrapper.classList.remove('menu-on');
        } else {
          let checkOpen = wrapper.classList.contains('menu-on');
          if (checkOpen) wrapper.classList.remove('menu-on');
          else wrapper.classList.add('menu-on');
        }
      },
      // beforeEnter(el, done) {
      //   TweenMax.fromTo(el, 1, {
      //     autoAlpha: 0,
      //     y: -100,
      //   }, {
      //     autoAlpha: 1,
      //     y: 0,
      //     // transformOrigin: '50% 50%',
      //     ease: Power4.easeOut,
      //     onComplete: done
      //   });
      // },
      // leave(el, done) {
      //   TweenMax.fromTo(el, 0.5, {
      //     autoAlpha: 1,
      //     y: 0,
      //   }, {
      //     autoAlpha: 0,
      //     y: -100,
      //     ease: Power4.easeOut,
      //     onComplete: done
      //   });
      // },
    },
    created() {
      EventBus.$on('pageName', (page) => {
        this.pageName2 = page;
      })
    },
    // watch: {
    //   $route(to, from) {
    //     console.log(to, from)
    //   }
    // },
    // beforeRouteUpdate(to, from, next) {

    //   console.log('app 호출됨')
    //   // if (next) {
    //   //   next();
    //   // }
    // }
  }
</script>

