<template>
  <footer :class="'footer' + (pageTitle === 'contact' ? ' mode-absolute' : '') + (pageTitle === 'credit' ? ' mode-credit' : '')">
    <div class="inner">
      <img :src="getSignImg()" alt="" class="signature">
      Copyright 2017{{ currentYearStr }}. <a href="http://allthatcode.com" target="_blank" class="blog">Sun-ung Mok</a> all rights reserved.
      <router-link to="./credit" class="btn_credit">
        CREDIT<i class="fa fa-long-arrow-right" aria-hidden="true"></i>
      </router-link>
    </div>
  </footer>
</template>

<script>
  export default {
    name: 'pageFooter',
    props: {
      pageTitle : String
    },
    data() {
      return {
        currentYearStr : null
      }
    },
    methods: {
      getSignImg : function(){
        let signImg = this.pageTitle === 'credit' ? 'signature_white.png' : 'signature.png'
        return require('../assets/images/' + signImg);
      },
      getCurrentYear : function(){
        let currentYear = new Date().getFullYear()
        let str = '-' + currentYear
        this.currentYearStr = str
      }
    },
    beforeMount() {
      this.getCurrentYear();
    }
  }
</script>

