import Vue from 'vue'
import Router from 'vue-router'
import Meta from 'vue-meta'
import Home from './views/home.vue'
import Interview from './views/interview.vue'
import Portfolio from './views/portfolio.vue'
import Contact from './views/contact.vue'
import Credit from './views/credit.vue'
// import view_portfolio from './components/portfolio.detail.vue'

Vue.use(Router)
Vue.use(Meta)
      // meta: {
      //   'title': titleDefault,
      //   'description': '웹크리에이터 목선웅 개인 웹사이트입니다. 소개, 포트폴리오 등을 포함하고 있습니다.',
      //   'keywords': '목선웅, moksunung, 개인 웹사이트, PR, 소개, 포트폴리오, 프론트엔드 개발자, 퍼블리셔, html, css, javascript, vue, vuejs, 뷰, 춘천',
      // }
export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '*',
      redirect: '/'
    },
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/interview',
      name: 'interview',
      component: Interview,
    },
    {
      path: '/portfolio',
      name: 'portfolio',
      component: Portfolio,
      props: true,
      children: [
        {
          path: ':post_number(\\d+)',
          name: 'portfolio.detail',
          component: Portfolio,
          props: true
        },
        {
          path: '/portfolio/web',
          name: 'web',
          component: Portfolio,
          props: true,
          children: [{
            path: ':post_number(\\d+)',
            name: 'web.detail',
            component: Portfolio,
            props: true
          }]
        },
        {
          path: '/portfolio/design',
          name: 'design',
          component: Portfolio,
          props: true,
          children: [{
            path: ':post_number(\\d+)',
            name: 'design.detail',
            component: Portfolio,
            props: true
          }]
        },
        {
          path: '/portfolio/video',
          name: 'video',
          component: Portfolio,
          props: true,
          children: [{
            path: ':post_number(\\d+)',
            name: 'video.detail',
            component: Portfolio,
            props: true
          }]
        },
        {
          path: '/portfolio/script',
          name: 'script',
          component: Portfolio,
          props: true,
          children: [{
            path: ':post_number(\\d+)',
            name: 'script.detail',
            component: Portfolio,
            props: true
          }]
        }
      ]
    },
    {
      path: '/contact',
      name: 'contact',
      component: Contact,
    },
    {
      path: '/credit',
      name: 'credit',
      component: Credit,
    }
  ],
  // scrollBehavior() {
  //   return {
  //     x: 0,
  //     y: 0
  //   };
  // },
    scrollBehavior: (to, from, savedPosition) => {
      let toMenu = to.path.split('/')[1];
      let fromMenu = from.path.split('/')[1];
      let savedPosition2 = toMenu !== fromMenu ? savedPosition : null;
        if (savedPosition2) {
          return savedPosition2;
        } else if (to.hash) {
          return {
            selector: to.hash
          };
        } else {
          if (toMenu !== fromMenu) {
            return {
              x: 0,
              y: 0
            };
          }
        }
      
    }
})

