<script>
import { EventBus } from '../event-bus'
import portfolioData from '../portfolio/portfolio.json'
// import view_details from '../components/portfolio.detail.vue'
import isotope from 'vueisotope'
import { ScrollToPlugin, TimelineLite, Circ } from 'gsap/all'
const plugins = [ ScrollToPlugin ]

export default {
  metaInfo: {
    // 페이지 제목 설정
    title: 'Portfolio',
    // 제목 템플릿 설정: "Vue 앱의 검색엔진 최적화 | yamoo9.gitbook.io/vue-a11y-seo"
    titleTemplate: '%s < Mok, Sunung',
    // <html> 요소의 속성 설정
    htmlAttrs: {
      // 주 언어 명시
      lang: 'ko-KR',
      dir: 'ltr'
    }
  },
  components: {
    isotope,
    // view_details
  },
  data() {
    return {
      layouts: [
        'masonry'
      ],
      list: portfolioData,
      currentLayout: 'masonry',
      selected: null,
      sortOption: 'original-order',
      filterOption: 'show all',
      option: {
        getFilterData: {
          'show all': function() {
            return true;
          },
          'web': function(el) {
            return el.category === 'web';
          },
          'design': function(el){
            return el.category === 'design'
          },
          'video': function(el){
            return el.category === 'video'
          },
          'script': function(el){
            return el.category === 'script'
          }
        },
        getSortData: {
          id: "id",
        },
      },
      detailData: null,
      contentsHeight: 0,
      keepScrollY: null,
      animationTimeLine: null
    }
  },
  methods: {
    track () {
      this.$ga.page('/')
    },
    barExistContributeWidth(num) {
      return parseInt(num) <= 0 ? 'width:100%;background: rgb(238, 238, 238);color:rgb(136, 136, 136);' : 'width:' + num + '%;';
    },
    barExistContribute(num) {
      return parseInt(num) <= 0 ? 'Nothing' : num + '%';
    },
    // animateNumber: function(num) {
      // var target = this.detailData.details.contribution;
      // var number = 0;
      // var animation = TweenMax.to(start, 5, {
      //   percentage: num, 
      //   onUpdate: function () {
      //     number = Math.ceil(start.percentage);
      //     return;
      //   }
      // });
      // console.log(number);
    // },
    handleScrollForward: function(event, el) {
      let scroll = window.scrollY;
      let bottom = scroll + document.body.clientHeight;
      let anchor = bottom - 50;
      let offsetValue = window.pageYOffset + el.getBoundingClientRect().top;
      if (anchor >= offsetValue) {
        el.classList.add('on');
      }
    },
    getFilteredUrl: function(eng){
      let filteredUrl;
      if (eng === 'show all') filteredUrl = '/portfolio'
      if (eng === 'web') filteredUrl = '/portfolio/web'
      if (eng === 'design') filteredUrl = '/portfolio/design'
      if (eng === 'video') filteredUrl = '/portfolio/video'
      if (eng === 'script') filteredUrl = '/portfolio/script'
      return filteredUrl;
    },
    getCategoryName: function(eng){
      let transKo;
      if (eng === 'show all') transKo = '모두 보기'
      if (eng === 'web') transKo = '웹기획/디자인/개발'
      if (eng === 'design') transKo = '그래픽/출판디자인'
      if (eng === 'video') transKo = '모션그래픽'
      if (eng === 'script') transKo = '스크립트'
      return transKo;
    },
    filter: function(key) {
      this.$refs.cpt.filter(key)
      this.mobileFilterOff()
    },
    handleScroll: (event, el) => {
      let scroll = window.scrollY;
      let bottom = scroll + document.body.clientHeight;
      let anchor = bottom - 50;
      let offsetValue = window.pageYOffset + el.getBoundingClientRect().top;
      if (anchor >= offsetValue) {
        el.classList.add('on');
      } else {
        el.classList.remove('on');
      }
    },
    returnThumbnailPath : function(thumbId){
      let thumbUrl = require('@/portfolio/' + thumbId + '/thumb.jpg');
      return thumbUrl;
    },
    returnDetaulImgPath : function(thumbnail, num){
      let thumbUrl = require('@/portfolio/' + thumbnail + '/' + num + '.jpg');
      return thumbUrl;
    },
    getCurrentCategory: function(str){
      let getActivedName = str === 'portfolio' ? 'show all' : str;
      this.filter(getActivedName);
    },
    findCurrentData: function(str){
      let obj = this.list.find(o => o.id === str);
      return obj;
    },
    loadOpenDetail(){
      if (this.$route.params.post_number) {
        this.detailData = this.findCurrentData(this.$route.params.post_number);
        this.openDetail(this.detailData)
      } else {
        this.closeDetail(false) 
      }
    },
    clickOpenDetail(info){
      this.$router.push({name: this.$route.name + '.detail', params: { post_number: info.id }})
    },
    killAnimationTimeline: function(){
      if (this.animationTimeLine !== null) this.animationTimeLine.kill();
    },
    openDetail: function(){
      let mobileCloser = this.$refs.mobileCloser
      this.keepScrollY = window.scrollY
      let eleOffsetTop = this.$refs.pageTop.clientHeight;
      
      let contentsCover = this.$refs.contentsCover
      let listCover = this.$refs.listCover
      this.killAnimationTimeline()
      let tl = new TimelineLite({onComplete: function(){
        listCover.style.height = '1px';
        if (mobileCloser.classList.contains('hidden')) mobileCloser.classList.remove('hidden');
        contentsCover.classList.add('active')
      }})
      tl.to(window, 0.3, { scrollTo:{y:eleOffsetTop - 80}, ease: Circ.easeInOut });
      // this.animationTimeLine = new TimelineLite({onComplete: function(){
      //   listCover.style.height = '1px';
      //   if (mobileCloser.classList.contains('hidden')) mobileCloser.classList.remove('hidden');
      // }})
      // this.animationTimeLine.to(window, 0.3, { scrollTo:{y:eleOffsetTop - 80}, ease: Circ.easeInOut }).to(contentsCover, 0.2, { xPercent:-50,, ease: Circ.easeInOut });
    },
    closeDetail: function(push){
      this.$refs.mobileCloser.classList.add('hidden');
      let thisComponent = this;
      let contentsCover = this.$refs.contentsCover;
      contentsCover.removeAttribute('style')
      this.$refs.listCover.removeAttribute('style')
      let toName = this.$route.name.split('.')[0]
      this.killAnimationTimeline()
      this.animationTimeLine = new TimelineLite({onComplete: function(){
        if (push !== false) {
          thisComponent.$router.push({ name: toName })
        }
        thisComponent.detailData = null
        contentsCover.classList.remove('active')
      }})
      // this.animationTimeLine.to(contentsCover, 0.2, { xPercent:0, ease: Circ.easeInOut });
      if (this.keepScrollY !== null) {
        this.animationTimeLine.to(window, 0.3, { scrollTo:{y:this.keepScrollY}, ease: Circ.easeInOut });
      }
    },
    mobileFilterOn: function(){
      this.$refs.filterLayer.classList.toggle('filter-on');
    },
    mobileFilterOff: function(){
      let layer = this.$refs.filterLayer;
      if (layer.classList.contains('filter-on')) layer.classList.remove('filter-on');
    }
  },
  beforeCreate() {
    EventBus.$emit('pageName', 'portfolio')
  },
  create() {
  },
  beforeMount() {
  },
  mounted() {
    this.loadOpenDetail()
    document.querySelector('section.intro').classList.add('loaded');
    this.$nextTick(function() {
      if (this.detailData === null) {
        this.getCurrentCategory(this.$route.name);
      }
    });
  },
  beforeRouteUpdate(to, from, next) {

    if (next) {
      let categoryName = to.name;
      let existInfo = categoryName.indexOf('.') > 0;
      if (existInfo === true) {
        this.detailData = this.findCurrentData(to.params.post_number);
        this.openDetail()
        categoryName = to.name.split('.')[0];
        // if (typeof(this.$refs.detailCover) != 'undefined' && this.$refs.detailCover != null) {
        //   console.log(this.detailData)
        // }
      } else {
        this.closeDetail(false)
      }
      this.getCurrentCategory(categoryName)
      next();
    }
  },
  
}
</script>

