<script>
import cover from '../components/cover.vue'
import { EventBus } from '../event-bus'

export default {
  metaInfo: {
    // 페이지 제목 설정
    title: 'Interview',
    // 제목 템플릿 설정: "Vue 앱의 검색엔진 최적화 | yamoo9.gitbook.io/vue-a11y-seo"
    titleTemplate: '%s < Mok, Sunung',
    // <html> 요소의 속성 설정
    htmlAttrs: {
      // 주 언어 명시
      lang: 'ko-KR',
      dir: 'ltr'
    }
  },
  components: {
    cover
  },
  methods: {
    track () {
      this.$ga.page('/interview')
    },
    handleScroll: (event, el) => {
      let scroll = window.scrollY;
      let bottom = scroll + document.body.clientHeight;
      let anchor = bottom - 50;
      let offsetValue = window.pageYOffset + el.getBoundingClientRect().top;
      if (anchor >= offsetValue) {
        el.classList.add('on');
      } else {
        el.classList.remove('on');
      }
    },
    // changePage: function(){
    //   var body = document.querySelector('body');
    //   body.className = '';
    //   body.setAttribute('id', this.$route.meta.page)
    // } 
  },
  beforeCreate() {
    EventBus.$emit('pageName', 'interview')
  },
  // beforeMount(){
  //   this.$route.meta.page
  // },
  // destroyed() {
  //   this.changePage();
  // }
}
